.swal2-container {
    z-index: 1102 !important;
}

.ant-modal-root {
    .ant-modal-mask {
        z-index: 1100 !important;
    }
    .ant-modal-wrap {
        z-index: 1101 !important;
    }
}

.bill-card {
    .bill-header {
        display: flex;
        align-items: center;

        .title {
            margin: auto;
        }
    }

    .list-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #f0f0f0;
    }

    .list-item:last-child {
        border-bottom: none;
    }

    .list-item-content {
        flex: 1;
        text-align: left;
    }

    .list-item-price {
        text-align: right;
        white-space: nowrap;
    }

    .list-item-service {
        flex: 1;
    }
}

